import domReady from 'domready';
import ProgressBar from 'progressbar.js';
import inView from 'in-view';

require('dark-mode-toggle');

const toggle = document.querySelector('dark-mode-toggle');
toggle.permanent = true;

const body = document.body;

function applyDarkTheme() {
  if (body.classList.contains('theme--light')) {
    body.classList.remove('theme--light');
  }

  body.classList.add('theme--dark');
}

function applyLightTheme() {
  if (body.classList.contains('theme--dark')) {
    body.classList.remove('theme--dark');
  }

  body.classList.add('theme--light');
}

function applyTheme() {
  toggle.mode === 'dark' ? applyDarkTheme() : applyLightTheme();
}

applyTheme();

toggle.addEventListener('colorschemechange', applyTheme);

domReady(function() {
  const currentYearElement = document.querySelector('#current-year');
  const now = new Date().getFullYear();

  if (currentYearElement) {
    currentYearElement.innerHTML = now;
  }

  const progresses = document.querySelectorAll('.js-progress');
  const lines = [...progresses].map(progress => {
    const { value, color } = progress.dataset;

    const line = new ProgressBar.Line(progress, {
      strokeWidth: 16,
      easing: 'easeInOut',
      duration: 1400,
      trailColor: '#e2e2e2',
      trailWidth: 16,
      svgStyle: { width: '100%', height: '100%', position: 'absolute' },
      color
    });

    return [line, value];
  });

  inView('.bar').once('enter', () =>
    lines.forEach(([line, value]) => line.animate(value))
  );
});
